@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
* {
  color: white;
  font-family: 'Open Sans', sans-serif;
}

a {
  color:rgb(165, 162, 148);
  font-weight: bolder;
}

html, body {height:100%;
}


body {

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  background: url(/Portfolio-Reactivated/static/media/newbg.e8e0e5f0.jpg) no-repeat center center fixed;
  background-size: cover;

  
}

.emph{
  font-style: italic;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.screenshot {
  width: auto;
  max-height: 80%;
  border: black;
  border-width: 3pt;
  border-style: solid;
}

.linkImg {
  max-width: 5%;
  height:auto;
  padding-right: 10px;
}

.homeText {
  font-family: 'Open Sans', sans-serif;
  font-size: 50pt;
  font-weight: bolder;
}

.buffer {
  margin-top: 10%;
}

.profilePic{
  max-height:50%;
  max-width:auto;
}



.buttonImg {
  width: 5%;
  height: auto;
  position:fixed;
  top:50%;

}


.vert {
  margin: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.aboutText{
  color:white;
  text-align: center;
  font-weight: bold;
  font-family: 'Open Sans', sans-serif;
  list-style-type: none;
}

.contentAlign{
 position:absolute;
 top:15%;

}


.nav-link {
  color:white;
}

.navbar-brand {
  color:white;
}


.hero-text {
  text-align: center;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}

.footy {

  position: fixed;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.459);
  color:white;
  height:10%;
  text-align: center;
  width:100%;

}

.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height:10%;
  background-color: black;
  color:white;
}

.linkPosition{
  position: fixed;
  right:0;
  top:0;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

